.no-tickets {
  font-size: 24px; /* Increase font size */
  font-weight: bold; /* Make it bold */
  text-align: center; /* Center the text */
  margin-top: 50px;
  margin-bottom: 100px; /* Adjust spacing */
}


.seating-chart {
    max-width: 600px;
    display: block;
    margin: 0 auto;
    width: 100%;
    height: auto;
  }

  /* Controls container for sorting & filtering */
.controls-container {
    display: flex;
    justify-content: space-evenly; /* Even spacing */
    align-items: center;
    flex-wrap: wrap; /* Wrap for smaller screens */
    margin-bottom: 20px;
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    max-width: 1200px;
    margin: 0 auto;
}

/* Price range & sort controls */
.price-range, .sort-controls, .source-filter {
    display: flex;
    gap: 15px;
    align-items: center;
}

/* Input and select styling */
.price-range input, 
.sort-controls select,
.source-filter select {
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* Labels styling */
.price-range label, .sort-controls label, .source-filter label {
    font-weight: bold;
}
/* Basic container for the tickets section */
.tickets-container {
    padding: 0px;
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }

  .ticket-info {
    display: flex;
    flex-direction: column;
    align-items: center; /* Ensures everything inside starts at the same left point */
}

.tickets-title {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 5px;
    text-align: center;
}

.game-details {
    font-size: 18px;
    text-align: center;
    margin-top: 0; /* Prevents unwanted extra spacing */
}

  /* Styling for the table displaying the ticket information */
  .tickets-table {
    padding: 20px;
    width: 100%;
    max-width: 1200px;
    border-collapse: collapse;
    /*margin-top: 20px;*/
    margin: 20px auto;
  }
  
  /* Table headers */
  .tickets-table th {
    background-color: #7f9665;
    color: white;
    padding: 10px;
    text-align: left;
  }
  
  /* Table rows */
  .tickets-table td {
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  /* Hover effect for rows */
  .tickets-table tr:hover {
    background-color: #f1f1f1;
  }
  
  /* Adjusting font size for readability */
  .tickets-table, .tickets-title, .tickets-table td, .tickets-table th {
    font-family: 'Arial', sans-serif;
    font-size: 1rem;
  }

  .tickets-table th {
    cursor: pointer;
    padding: 10px;
    background: #f4f4f4;
    text-align: left;
    border-bottom: 2px solid #ddd;
    user-select: none;
}

.tickets-table th:hover {
    background: #788d5f;
}

.tickets-table th::after {
    content: "";
    margin-left: 5px;
}

.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: flex-start;  /* Align to top */
  width: 100%;
  max-width: 1200px;
  gap: 20px; /* Space between left and right columns */
}

/* Left Column: Fixed position */
.left-column {
  display: flex;
  flex-direction: column;
  width: 60%;
  position: sticky;
  top: 10px; /* Keeps it stuck to the top while scrolling */
  height: 89vh; /* Keeps it from taking up too much space */
  /*overflow-y: auto; /* Allows scrolling if content is too tall */
  /* padding: 10px; */
  border-radius: 10px;
}

/* Right Column: Scrollable tickets */
.right-column {
  width: 40%;
  max-height: 89vh; /* Prevents overflowing the viewport */
  overflow-y: auto; /* Allows scrolling */
  padding: 10px;
}

.tickets-table {
  border-collapse: collapse;
  width: 100%;
}

.tickets-table thead {
  position: sticky;
  top: 0;
  z-index: 3; /* Ensures it's above ticket rows */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Enhances separation */
  background-color: #6b7f4b;
}

.tickets-table th {
  background-color: white;
  padding: 10px;
  border-bottom: 2px solid black;
  text-align: left;
}


/* Responsive: Stack everything on small screens */
@media (max-width: 900px) {
  .main-content {
      flex-direction: column;
  }

  .left-column,
  .right-column {
      width: 100%;
      position: static; /* Remove fixed behavior */
      height: auto;
  }

  .right-column {
      max-height: none;
      overflow: visible;
  }
}

@media (min-width: 901px) {
  .page-container {
      overflow: hidden;
      height: 100%;
  }
}